<template>
  <div>
    <b-container class="mt-4">
      <b-row>
        <b-col md="12" class="text-center" v-if="is_loading">
          <b-spinner variant="primary" label="Spinning"></b-spinner>
        </b-col>
        <b-col md="12" class="text-center" v-if="!is_loading && !Object.keys(this.document_signees).length && this.document_signees.constructor === Object">
          <p>No Records Found.</p>
        </b-col>
        <b-modal
          ref="confirm-modal"
          id="confirm-modal"
          no-close-on-esc
          v-if="!is_loading && this.sign_key && this.sign_key !== 'null'"
          no-close-on-backdrop
          hide-header-close
          title="Please enter the secret code"
          hide-footer
          centered
        >
          <SignAuthenticationForm :sign_key="sign_key" @authenticationSuccess="authenticationSuccess" />
        </b-modal>
        <b-col lg="12">
          <Esign
            :max_image_size="525"
            :src="s3_url"
            :s3_config="s3_config_object"
            :users_list="users"
            :edit_tools="edit_tools"
            :active_users_list="active_user"
            :show_save_button="show_save_button"
            ref="esign_component_ref"
            @input="save($event)"
            :is_public="true"
            v-if="is_authenticated"
            v-model="tools"
          >
            <template #process_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner>
              </div>
            </template>
            <template #viewer_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner></div
            ></template>
            <template #thumbnails_loader>
              <div class="text-center">
                <b-spinner variant="primary" label="Spinning"></b-spinner></div
            ></template>
          </Esign>
        </b-col>
        <b-col v-if="status" class="text-right">
          <span class="badge badge-soft-danger">{{ status }}</span>
        </b-col>
        <b-col lg="12" class="d-flex justify-content-between align-items-center my-4" v-if="is_authenticated">
          <p class="mb-0"><strong>Note: </strong>Once data is submitted, it cannot be undone. Please recheck before you Finish.</p>
          <b-button variant="primary" size="sm" @click="saveDocument" :disabled="button_disabled">Finish <b-spinner v-if="button_disabled" small label="Spinning smalll"></b-spinner></b-button>
        </b-col>
      </b-row>
      <SaveResponseMessage v-if="show_response" />
    </b-container>
  </div>
</template>

<script>
  import Esign from "./esign/AcEsign";
  import SignAuthenticationForm from "./SignAuthenticationForm";
  import SaveResponseMessage from "./SaveResponseMessage";
  import Documents from "@/js/sign/documents.js";
  import Data from "@/js/core/data";
  export default {
    components: { Esign, SignAuthenticationForm, SaveResponseMessage },
    data() {
      return {
        is_loading: false,
        document_signees: {},
        button_disabled: false,
        show_response: false,
        is_authenticated: false,
        s3_config_object: {
          url: "",
          id: "file_id",
          name: "file_name",
        },
        users: [],
        edit_tools: ["text", "date", "signature"],
        show_save_button: false,
        active_user: [],
        tools: [],
        s3_url: "",
        db_data: {},
        result: "",
        sign_key: "",
        data: new Data(),
        status: "",
      };
    },
    methods: {
      authenticationSuccess() {
        this.is_authenticated = true;
        this.$refs["confirm-modal"].hide();
      },
      // Get Document Signees Details
      async getDocumentSignees() {
        try {
          this.document_signees = await Documents.get_document_signees(this.$route.params.document_id, window.domain);
          this.active_user = JSON.parse(this.document_signees.signee_details).filter((document_signee) => document_signee.id == this.$route.params.contact_id);
          this.s3_url = this.data.getPCors(this.document_signees.url);
          this.s3_config_object.name = this.document_signees.url.split("/").slice(-1)[0];
          this.users = JSON.parse(this.document_signees.signee_details);
          this.sign_key = this.users.filter((active_user) => active_user.id == this.$route.params.contact_id)[0].sign_key;
          this.tools = JSON.parse(this.document_signees.doc_json).tools;
          this.is_loading = false;
        } catch (error) {
          this.is_loading = false;
          console.log(error);
        }
      },
      // Save the tools data
      async save($event) {
        try {
          this.button_disabled = true;
          this.tools = $event;
          this.db_data = {};
          let doc_json = {};
          doc_json.tools = $event;
          this.db_data.doc_json = [];
          this.db_data.doc_json.push(doc_json);
          this.db_data.doc_json = JSON.stringify(this.db_data.doc_json);
          this.db_data.doc_json = this.db_data.doc_json.substring(1, this.db_data.doc_json.length - 1);
          this.db_data.status = "Signed";
          this.db_data.is_all_users_signed = 0;

          // Check user tools are empty value or not
          var tool_validation = false;
          this.tools.forEach((tool) => {
            if (tool.user_id == this.$route.params.contact_id && !tool.props?.value) {
              tool_validation = true;
              this.status = "Please fill the tools before save the document!!";
            }
          });
          if (tool_validation) {
            setTimeout(() => {
              this.button_disabled = false;
              this.status = "";
            }, 1500);
            return;
          }
          let current_user = this.users.filter((user) => user.status !== "Signed");
          // Updating the document status to 'Signed' once the all users signed
          if (current_user.length == 1 && current_user[0].id == this.active_user[0].id) {
            this.db_data.is_all_users_signed = 1;
          }
          let response = await Documents.update_document_data(window.domain, this.$route.params.document_id, this.$route.params.contact_id, this.db_data, window.user);
          console.log("response", response);
          if (response?.status) {
            setTimeout(() => {
              this.button_disabled = false;
              this.is_authenticated = false;
              this.show_response = true;
              this.status = "";
            }, 1500);
          }
        } catch (error) {
          console.log(error);
          this.button_disabled = false;
        }
      },
      // Save the signees document data in DB
      saveDocument() {
        this.$refs.esign_component_ref.$refs["ac-esign-toolbar"].save();
      },
    },
    async mounted() {
      this.is_loading = true;
      await this.getDocumentSignees();
      if (this.sign_key && this.sign_key !== "null") this.$refs["confirm-modal"].show();
      else if (Object.keys(this.document_signees).length && this.document_signees.constructor === Object) this.is_authenticated = true;
    },
  };
</script>

<style lang="scss" scoped></style>
